import { h, text, app } from "hyperapp";

const RedCard = () => {}
const BlueCard = () => {}

const CardButton = (label, clickEvent) => 
    h("button", {
        type: "button",
        className: "card-button",
        onClick: clickEvent 
    },[
        text(label)
    ]);

app({
    init: { cards: []},
    view: ({cards}) =>
        h("main", {}, [
            h("h1", { className: "headline"}, text("Hacker Strategies!"),
            CardButton("red", RedCard),
            CardButton("blue", BlueCard))
        ])
    ,
    node: document.getElementById("app")
});
